body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
}
body {
  background-color: #000000;
  background-image: url('https://images.unsplash.com/photo-1467685790346-20bfe73a81f0?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1950&q=80');
  color: white;
  cursor: pointer;
}



.glow {
    font-size: 80px;
    color: #fff;
    text-align: center;
    -webkit-animation: glow 1s ease-in-out infinite alternate;
    animation: glow 1s ease-in-out infinite alternate;
  }
  
  @-webkit-keyframes glow {
    from {
      text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #923CB5, 0 0 40px #923CB5, 0 0 50px #923CB5, 0 0 60px #923CB5, 0 0 70px #923CB5;
    }
    to {
      text-shadow: 0 0 20px #fff, 0 0 30px #7552ce, 0 0 40px #7552ce, 0 0 50px #7552ce, 0 0 60px #7552ce, 0 0 70px #7552ce, 0 0 80px #7552ce;
    }
  }
img {
    position: relative;
    float: left;
}

